/* blinker-200 - latin */
@font-face {
  font-family: 'Blinker';
  font-style: normal;
  font-weight: 200;
  src: local('Blinker ExtraLight'), local('Blinker-ExtraLight'),
       url('./blinker-v1-latin-200.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('./blinker-v1-latin-200.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
